import React, {Component} from 'react'
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, TabContent, TabPane } from 'reactstrap'
import classNames from 'classnames/bind'
import styles from './Category.css'
import { Link } from 'react-router-dom'
import Header from '../Header/Header'
import Footer from '../Footer/Footer'
import { getCategory } from '../../api'
import Financial from 'imgs/demo/Financial.png'
import Business from 'imgs/demo/Business.png'
import Magazine from 'imgs/demo/Magazine.png'
import Restaurant from 'imgs/demo/Restaurant.png'
import Sport from 'imgs/demo/Sport.png'
import Technology from 'imgs/demo/Technology.png'
import Travel from 'imgs/demo/Travel.png'
let imgObject = {
  Financial: Financial,
  Business: Business,
  Magazine: Magazine,
  Restaurant: Restaurant,
  Sport: Sport,
  Technology: Technology,
  Travel: Travel
}
const cx = classNames.bind(styles)

class Category extends Component {
  constructor (props) {
    super(props)

    this.state = {
      activeTab: 0,
      demoData: null,
      modal: false
    }
  }
  componentDidMount () {
    getCategory()
      .then(
        data => console.log(data) || this.setState({demoData: data})
      )
      .catch(
        error => console.log(error) || this.toggleModal()
      )
  }
  toggleBtn = (id) => {
    this.setState({activeTab: id})
  }
  toggleModal = () => {
    this.setState({
      modal: !this.state.modal
    })
  }
  render () {
    return (
      <div id='Category' className={cx('Category', 'component-container')}>
        <Modal isOpen={this.state.modal} className={this.props.className}>
          <ModalHeader>Error</ModalHeader>
          <ModalBody>
            Hello, there is error with you request end point, we will direct you to the previous page.
          </ModalBody>
          <ModalFooter>
            <Button
              color='danger'
              onClick={() => {
                this.toggleModal()
                this.props.history.push('/')
              }}
            > OK
            </Button>
          </ModalFooter>
        </Modal>
        <Header />
        <div className={cx('Category__header')}>
          <h1>Lorem ipsum dolor sit amet</h1>
          <p>Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam, eaque ipsa quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt explicabo.</p>
        </div>
        <div className={cx('Category__selection')}>
          <button className={this.state.activeTab === 0 ? 'active' : ''} onClick={() => { this.toggleBtn(0) }}>Lorem imsum</button>
          <button className={this.state.activeTab === 1 ? 'active' : ''} onClick={() => { this.toggleBtn(1) }}>Dolor sit</button>
          <button className={this.state.activeTab === 2 ? 'active' : ''} onClick={() => { this.toggleBtn(2) }}>Category C</button>
          <button className={this.state.activeTab === 3 ? 'active' : ''} onClick={() => { this.toggleBtn(3) }}>Category D</button>
          <button className={this.state.activeTab === 4 ? 'active' : ''} onClick={() => { this.toggleBtn(4) }}>Category E</button>
          <button className={this.state.activeTab === 5 ? 'active' : ''} onClick={() => { this.toggleBtn(5) }}>Category F</button>
        </div>
        <TabContent activeTab={this.state.activeTab}>
          {
            this.state.demoData
              ? this.state.demoData.map((item, idx) => {
                return (
                  <TabPane tabId={idx}>
                    <div className={cx('Category__content')}>
                      <div className={cx('Category__content_list')}>
                        {
                          item.value.map((element) => (
                            <div id={item.subject + element.id} className={cx('Category__content_listItem')}>
                              <Link
                                to={{
                                  pathname: `/category/${item.subject}/${element.id}`
                                }}
                              >
                                <img src={imgObject[element.src]} alt='ad' />
                              </Link>
                            </div>
                          ))
                        }
                      </div>
                    </div>
                  </TabPane>
                )
              })
              : 'rendering'
          }
        </TabContent>
        <Footer />
      </div>
    )
  }
}

export default Category



