import {
  Collapse,
  Navbar,
  NavbarToggler,
  Nav,
  NavItem,
  Button
} from 'reactstrap'
import classNames from 'classnames/bind'
import styles from './DeviceOptionNav.css'
import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import pcActive from 'imgs/preview/pc-active.png'
import pc from 'imgs/preview/pc.png'
import smphoneActive from 'imgs/preview/smartphone-active.png'
import smphone from 'imgs/preview/smartphone.png'
import smphoneHrActive from 'imgs/preview/smartphone-hr-active.png'
import smphoneHr from 'imgs/preview/smartphone-hr.png'
import tabletActive from 'imgs/preview/tablet-active.png'
import tablet from 'imgs/preview/tablet.png'
import tabletHrActive from 'imgs/preview/tablet-hr-active.png'
import tabletHr from 'imgs/preview/tablet-hr.png'

const cx = classNames.bind(styles)
const dataArr = [
  {spec: 'Desktop', src: pc, activeSrc: pcActive},
  {spec: 'tabVertical', src: tablet, activeSrc: tabletActive},
  {spec: 'tabHorizontal', src: tabletHr, activeSrc: tabletHrActive},
  {spec: 'phoneVertical', src: smphone, activeSrc: smphoneActive},
  {spec: 'phoneHorizontal', src: smphoneHr, activeSrc: smphoneHrActive}
]

class DeviceOptionNav extends Component {
  constructor (props) {
    super(props)
    this.state = {
      isOpen: false,
      activatedBtn: 'Desktop'
    }
  }
  toggle = () => {
    this.setState({
      isOpen: !this.state.isOpen
    })
  }
  toggleBtnStatus = (activeDevice) => {
    this.setState({activatedBtn: activeDevice})
  }
  render () {
    return (
      <Navbar light expand='md' className={`${cx('DeviceOptionNav')} text-secondary`}>
        <Link onClick={window.scrollTo(0, 0)} to='/' className='text-secondary'>
          <div className={cx('DeviceOptionNav__logo')}>JadeitGreen</div>
        </Link>
        <NavbarToggler onClick={this.toggle} />
        <Collapse className={cx('DeviceOptionNav__list')} isOpen={this.state.isOpen} navbar>
          <Nav navbar>
            {
              dataArr.map((item) => (
                <NavItem className={cx('DeviceOptionNav__listItem')}>
                  <Link to={item.spec} onClick={() => { this.toggleBtnStatus(item.spec) }} className='text-secondary'>
                    <img src={this.state.activatedBtn === item.spec ? item.activeSrc : item.src} alt={item.spec} />
                  </Link>
                </NavItem>
              ))
            }
            <Button className={cx('DeviceOptionNav__listItem', 'DeviceOptionNav__listItem--button')} color='primary'>Select this template</Button>
          </Nav>
        </Collapse>
      </Navbar>
    )
  }
}
export default DeviceOptionNav



