const apiCategory = 'https://my-json-server.typicode.com/jadeitgreen/jadeitgreen-db/category'
const apiDemo = 'https://my-json-server.typicode.com/jadeitgreen/jadeitgreen-db/demo'

export function getCategory () {
  return fetch(apiCategory)
    .then(response => {
      return response.json()
    })
    .catch(error => {
      return error
    })
}
export function getDemoUrl (subject, websiteId) {
  // return fetch(`${apiDemo}/${subject}/${websiteId}`)
  return fetch(`${apiDemo}`)
    .then(response => {
      // return response.json()
      return response.json()
    })
    .catch(error => {
      return error
    })
}



