import React from 'react'
import classNames from 'classnames/bind'
import styles from './Client.css'
import ntucep from 'imgs/case/ntucep.png'
import flipweb from 'imgs/case/flipweb.png'
import brandnewstar from 'imgs/case/brandnewstar.png'
import ntuaf from 'imgs/case/ntuaf.jpg'
const cx = classNames.bind(styles)
const Client = (props) => {
  let clientArr = [
    {url: 'http://cep.ntu.edu.tw', src: ntucep},
    {url: 'http://flipweb.tw', src: flipweb},
    {url: 'http://brandnewstar.asia', src: brandnewstar},
    {url: 'http://ntuartfest.org', src: ntuaf}
  ]
  return (
    <div id='client' className={cx('Client')}>
      <div className={cx('Client__header')}>
        <h1>Our Client</h1>
        <p>Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam, eaque ipsa quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt explicabo.</p>
      </div>
      <div className={cx('Client__content')}>
        <div className={cx('Client__content_list')}>
          {
            clientArr.map(item => {
              return (
                <div className={cx('Client__content_listItem')}>
                  <a href={item.url} target='_blank'><img src={item.src} alt={item.url} /></a>
                </div>
              )
            })
          }
        </div>
      </div>
    </div>
  )
}

export default Client



