import {
  Collapse,
  Navbar,
  NavbarToggler,
  Nav,
  NavItem,
  Button
} from 'reactstrap'
import classNames from 'classnames/bind'
import styles from './Header.css'
import React, { Component } from 'react'
import { Link, withRouter } from 'react-router-dom'
const cx = classNames.bind(styles)

class Header extends Component {
  constructor (props) {
    super(props)
    this.state = {
      isOpen: false
    }
  }
  toggle = () => {
    this.setState({
      isOpen: !this.state.isOpen
    })
  }
  render () {
    return (
      <Navbar light expand='md' className={`${cx('Header')} text-secondary`}>
        <Link onClick={() => { window.scrollTo(0, 0) }} to='/' className='text-secondary'>
          <div className={cx('Header__logo')}>JadeitGreen</div>
        </Link>
        <NavbarToggler onClick={this.toggle} />
        <Collapse className={cx('Header__list')} isOpen={this.state.isOpen} navbar>
          <Nav navbar>
            <NavItem className={cx('Header__listItem')}>
              <a href='/#demo' className='text-secondary'>LOREM</a>
            </NavItem>
            <NavItem className={cx('Header__listItem')}>
              <a href='/#price' target='_top' className='text-secondary'>IPSUM</a>
            </NavItem>
            <NavItem className={cx('Header__listItem')}>
              <a href='/#client' target='_top' className='text-secondary'>DOLOR</a>
            </NavItem>
            <NavItem className={cx('Header__listItem')}>
              <a href='/#contact' target='_top' className='text-secondary'>SIT</a>
            </NavItem>
            <Button className={cx('Header__listItem', 'Header__listItem--button')} color='primary'>Contact now</Button>
          </Nav>
        </Collapse>
      </Navbar>
    )
  }
}
export default withRouter(Header)



