import React from 'react'
import classNames from 'classnames/bind'
import styles from './Demo.css'
import { Link } from 'react-router-dom'
import Financial from 'imgs/demo/Financial.png'
import Business from 'imgs/demo/Business.png'
import Magazine from 'imgs/demo/Magazine.png'
import Restaurant from 'imgs/demo/Restaurant.png'
import Sport from 'imgs/demo/Sport.png'
import Technology from 'imgs/demo/Technology.png'
import Travel from 'imgs/demo/Travel.png'
let array = [
  {src: Financial, name: 'Financial'},
  {src: Business, name: 'Business'},
  {src: Magazine, name: 'Magazine'},
  {src: Restaurant, name: 'Restaurant'},
  {src: Sport, name: 'Sport'},
  {src: Technology, name: 'Technology'},
  {src: Travel, name: 'Travel'},
  {src: Travel, name: 'Travel'},
  {src: Travel, name: 'Travel'}
]
const cx = classNames.bind(styles)
const Demo = (props) => {
  // console.log(imgs)
  return (
    <div id='demo' className={cx('Demo', 'component-container')}>
      <div className={cx('Demo__header')}>
        <h1>Lorem ipsum dolor sit amet</h1>
        <p>Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam, eaque ipsa quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt explicabo.</p>
      </div>
      <div className={cx('Demo__content')}>
        <div className={cx('Demo__content_list')}>
          {
            array.map(item => {
              return (
                <div className={cx('Demo__content_listItem')}>
                  <Link to='/category'>
                    <img src={item.src} alt='ad' />
                    <h4>{item.name}</h4>
                  </Link>
                </div>
              )
            })
          }
        </div>
      </div>
    </div>
  )
}

export default Demo



