import React from 'react'
import classNames from 'classnames/bind'
import styles from './Footer.css'

const cx = classNames.bind(styles)
const Footer = (props) => {
  return (
    <div className={cx('Footer', 'component-container')}>
      <div className={cx('Footer__content', 'Footer__content_logo')}>
        <div>
          <img src='https://fakeimg.pl/60x60/239C75/fff?text=logo' alt='logo' />
          <h3>Logo</h3>
        </div>
        <p>Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat</p>
      </div>
      <div className={cx('Footer__content', 'Footer__content_contact')}>
        <h3>Contact Us</h3>
        <p>Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat</p>
        <div>
          <p>Phone: 0800-092-009</p>
          <p>Line: @blablabla</p>
        </div>
      </div>
      <div className={cx('Footer__content', 'Footer__content_qrcode')}>
        <img src='https://fakeimg.pl/200x200?text=QRCode' alt='qrcode' />
      </div>
    </div>
  )
}

export default Footer



