import { Button } from 'reactstrap'
import React from 'react'
import classNames from 'classnames/bind'
import styles from './Price.css'
const planArr = [
  {
    name: 'Plan A',
    price: '24,000',
    detail: ['Lorem ipsum', 'Lorem ipsum', 'Lorem ipsum', 'Lorem ipsum', 'Lorem ipsum'],
    isPopular: false
  },
  {
    name: 'Plan B',
    price: '36,000',
    detail: ['Lorem ipsum', 'Lorem ipsum', 'Lorem ipsum', 'Lorem ipsum', 'Lorem ipsum'],
    isPopular: true
  },
  {
    name: 'Plan C',
    price: '48,000',
    detail: ['Lorem ipsum', 'Lorem ipsum', 'Lorem ipsum', 'Lorem ipsum', 'Lorem ipsum'],
    isPopular: false
  }
]
const cx = classNames.bind(styles)
const Price = (props) => {
  return (
    <div id='price' className={cx('Price', 'component-container')}>
      <div className={cx('Price__content')}>
        <div className={cx('Price__content_list')}>
          {
            planArr.map(item => {
              return (
                <div className={ item.isPopular ? cx('Price__content_list_item', 'Price__content_list_item--active') : cx('Price__content_list_item')}>
                  <div className={cx('Price__listItem_header')}>
                    <h3>{item.name}</h3>
                  </div>
                  <div className={cx('Price__listItem_content')}>
                    <h3>{item.price}</h3>
                    <h4>Lorem ipsum</h4>
                  </div>
                  <div className={cx('Price__listItem_detail')}>
                    {item.detail.map(desc => (<h4>{desc}</h4>))}
                  </div>
                  <footer className={cx('Price__listItem_action')}>
                    <Button color='primary'>Contact Now</Button>
                  </footer>
                </div>
              )
            })
          }
        </div>
      </div>
    </div>
  )
}

export default Price



