import React, { Component } from 'react'
import classNames from 'classnames/bind'
import styles from './MultipleDeviceDemo.css'
import DeviceOptionNav from './DeviceOptionNav/DeviceOptionNav'
import {getDemoUrl} from '../../api'
import { Route, Redirect } from 'react-router-dom'
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap'

const cx = classNames.bind(styles)
class MultipleDeviceDemo extends Component {
  constructor (props) {
    super(props)
    this.state = {
      renderUrl: '',
      modal: false
    }
  }
  componentDidMount () {
    let {subject, id} = this.props.match.params

    getDemoUrl(subject, id)
      .then(
        // data => console.log(data) || this.setState({renderUrl: data})
        data => console.log(data) || this.setState({renderUrl: data[subject][id]})
      )
      .catch(
        error => console.log(error) || this.toggleModal()
      )
  }
  toggleModal = () => {
    this.setState({
      modal: !this.state.modal
    })
  }
  render () {
    let {subject, id} = this.props.match.params
    return (
      <div className={cx('MultipleDeviceDemo')}>
        <Modal
          isOpen={this.state.modal}
          className={this.props.className}>
          <ModalHeader>Error</ModalHeader>
          <ModalBody>
            Hello, there is error with you request end point, we will direct you to the previous page.
          </ModalBody>
          <ModalFooter>
            <Button
              color='danger'
              onClick={() => {
                this.toggleModal()
                this.props.history.push('/category')
              }}
            > OK
            </Button>
          </ModalFooter>
        </Modal>

        <DeviceOptionNav
          subject={subject}
          id={id}
        />

        <Route path={`/category/${subject}/${id}/desktop`}
          render={() =>
            <div className={cx('MultipleDeviceDemo__body', 'MultipleDeviceDemo__body--desktop')}>
              <iframe
                src={this.state.renderUrl}
                title='Desktop Demo'
              />
            </div>
          }
        />
        <Route path={`/category/${subject}/${id}/tabVertical`}
          render={() =>
            <div className={cx('MultipleDeviceDemo__body', 'MultipleDeviceDemo__body--tabVertical')}>
              <iframe
                src={this.state.renderUrl}
                title='Tablet Demo'
              />
            </div>
          }
        />
        <Route path={`/category/${subject}/${id}/tabHorizontal`}
          render={() =>
            <div className={cx('MultipleDeviceDemo__body', 'MultipleDeviceDemo__body--tabHorizontal')}>
              <iframe
                src={this.state.renderUrl}
                title='Tablet Demo'
              />
            </div>
          }
        />
        <Route path={`/category/${subject}/${id}/phoneVertical`}
          render={() =>
            <div className={cx('MultipleDeviceDemo__body', 'MultipleDeviceDemo__body--phoneVertical')}>
              <iframe
                src={this.state.renderUrl}
                title='Phone Demo'
              />
            </div>
          }
        />
        <Route path={`/category/${subject}/${id}/phoneHorizontal`}
          render={() =>
            <div className={cx('MultipleDeviceDemo__body', 'MultipleDeviceDemo__body--phoneHorizontal')}>
              <iframe
                src={this.state.renderUrl}
                title='Phone Demo'
              />
            </div>
          }
        />
        <Redirect from={`/category/${subject}/${id}/`} to={`/category/${subject}/${id}/desktop`} />
      </div>
    )
  }
}

export default MultipleDeviceDemo



