import React from 'react'
import classNames from 'classnames/bind'
import styles from './Jumbo.css'
import computer from 'imgs/header/bg-1.png'
const cx = classNames.bind(styles)
const Jumbo = (props) => {
  return (
    <div id='jumbo' className={cx('jumbo')}>
      <h1 className={cx('jumbo__header')}>LOREM<hr />IPSUM</h1>
      <p className={cx('jumbo__description')}>Lorem ipsum dolor <hr />sit amet</p>
      <img alt='logo' src={computer} />
    </div>
  )
}

export default Jumbo



