import React, {Component} from 'react'
import classNames from 'classnames/bind'
import styles from './Contact.css'
import {Button} from 'reactstrap'
import phone from 'imgs/contact/phone.png'
import line from 'imgs/contact/line.png'
import location from 'imgs/contact/location.png'
const cx = classNames.bind(styles)
class Contact extends Component {
  constructor (props) {
    super(props)
    this.state = {
      name: '',
      email: '',
      phone: '',
      idea: ''
    }
  }
  handleSubmit = (e) => {
    e.preventDefault()
    // Need to give proper post address
    console.log(this.state)
    this.setState({name: '', email: '', phone: '', idea: ''})
  }
  componentDidMount () {
    if (!window.google) {
      const s = document.createElement('script')
      s.type = 'text/javascript'
      s.src = `https://maps.google.com/maps/api/js?key=AIzaSyB6IcyRn40t5OZWJ7daYqrxMQ5ZFeafN7E`
      const x = document.getElementsByTagName('script')[0]
      x.parentNode.insertBefore(s, x)
      s.addEventListener('load', e => {
        return new window.google.maps.Map(document.getElementById('googleMap'), {
          center: { lat: 41.0082, lng: 28.9784 },
          zoom: 8
        })
      })
    } else {
      return new window.google.maps.Map(document.getElementById('googleMap'), {
        center: { lat: 41.0082, lng: 28.9784 },
        zoom: 8
      })
    }
  }
  render () {
    return (
      <div id='contact' className={cx('Contact', 'component-container')}>
        <div className={cx('Contact__body')}>
          <div className={cx('Contact__body_header')}>
            <h1>Contact Us</h1>
          </div>
          <div className={cx('Contact__body_content')}>
            <div className={cx('Contact__body_contentInfo')}>
              <h1>Interested?</h1>
              <h4>Contact us with following ways:</h4>
              <div>
                <h3><img src={phone} alt='phone' />0800-092-009</h3>
                <h3><img src={line} alt='line' />@blablabla</h3>
                <h3><img src={location} alt='phone' />No. 87, Baker st.</h3>
                <div id='googleMap' />
              </div>
            </div>
            <div className={cx('Contact__body_contentForm')}>
              <h1>Or</h1>
              <h4>Fill the contact below and we will</h4>
              <div>
                <form onSubmit={this.handleSubmit}>
                  <label for='name'>Name<input onChange={(e) => { this.setState({name: e.target.value}) }} value={this.state.name} type='text' id='name' /></label>
                  <label for='email'>E-mail<input onChange={(e) => { this.setState({email: e.target.value}) }} value={this.state.email} type='text' id='email' /></label>
                  <label for='phone'>Phone<input onChange={(e) => { this.setState({phone: e.target.value}) }} value={this.state.phone} type='text' id='phone' /></label>
                  <label for='idea'>What kind of Website do you prefer?<textarea onChange={(e) => { this.setState({idea: e.target.value}) }} value={this.state.idea} maxlength='500' id='idea' /></label>
                  <Button type='submit' color='primary' outline size='lg' block>SUBMIT</Button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Contact



